export default "import { ArrayDataSource } from \"@angular/cdk/collections\";\nimport { NestedTreeControl } from \"@angular/cdk/tree\";\nimport { Component } from \"@angular/core\";\nimport { expand } from \"@nova-ui/bits\";\n\n\ninterface FoodNode {\n    name: string;\n    children?: FoodNode[];\n}\n\nconst TREE_DATA: FoodNode[] = [\n    {\n        name: \"Fruit\",\n        children: [\n            { name: \"Apple\" },\n            { name: \"Banana\" },\n            { name: \"Fruit loops\" },\n        ],\n    },\n    {\n        name: \"Vegetables\",\n        children: [\n            {\n                name: \"Green\",\n                children: [{ name: \"Broccoli\" }, { name: \"Brussels sprouts\" }],\n            },\n            {\n                name: \"Orange\",\n                children: [{ name: \"Pumpkins\" }, { name: \"Carrots\" }],\n            },\n            {\n                name: \"Red\",\n            },\n        ],\n    },\n    {\n        name: \"Meat\",\n    },\n];\n\n\n@Component({\n    selector: \"nui-tree-basic-example\",\n    templateUrl: \"tree-basic.example.component.html\",\n    styleUrls: [\"tree-basic.example.component.less\"],\n    host: { id: \"nui-tree-basic-example\" },\n    animations: [expand],\n})\nexport class TreeBasicExampleComponent {\n    treeControl = new NestedTreeControl<FoodNode>((node) => node.children);\n    dataSource = new ArrayDataSource(TREE_DATA);\n\n    hasChild = (_: number, node: FoodNode) => !!node.children && node.children.length > 0;\n}\n";