export default "@import (reference) \"mixins/expander-base\";\n\n.nui-tree {\n    .expander();\n\n    &__header-title {\n        width: 100%;\n        line-height: 18px;\n    }\n\n    &__body {\n        &--collapsed {\n            overflow: hidden;\n        }\n    }\n\n    &__leaf {\n        display: flex;\n        align-items: center;\n\n        &-title {\n            width: 100%;\n        }\n    }\n\n    &__invisible {\n        display: none;\n    }\n\n    .nui-badge-counter {\n        position: relative;\n        height: 18px;\n\n        &:after {\n            background: #555;\n            width: 45px;\n            height: 18px;\n            line-height: 18px;\n            top: inherit;\n        }\n    }\n}\n";