export default "import { AfterViewInit, ChangeDetectorRef, Component, OnDestroy, ViewChild } from \"@angular/core\";\nimport {\n    ClientSideDataSource,\n    INovaFilteringOutputs, PaginatorComponent, SearchComponent, SorterComponent,\n} from \"@nova-ui/bits\";\nimport { Subject } from \"rxjs\";\nimport { Subscription } from \"rxjs\";\nimport { debounceTime } from \"rxjs/operators\";\n\nconst INITIAL_ARRAY = [\n    {color: \"regular-blue\"},\n    {color: \"regular-green\"},\n    {color: \"regular-yellow\"},\n    {color: \"regular-cyan\"},\n    {color: \"regular-magenta\"},\n    {color: \"regular-black\"},\n    {color: \"dark-blue\"},\n    {color: \"dark-green\"},\n    {color: \"dark-yellow\"},\n    {color: \"dark-cyan\"},\n    {color: \"dark-magenta\"},\n    {color: \"light-blue\"},\n    {color: \"light-green\"},\n    {color: \"light-yellow\"},\n    {color: \"light-cyan\"},\n    {color: \"light-magenta\"},\n];\n\n@Component({\n    selector: \"nui-client-side-delayed-data-source-example\",\n    providers: [ClientSideDataSource],\n    templateUrl: \"./client-side-delayed.example.component.html\",\n})\nexport class DataSourceClientSideDelayedExampleComponent implements AfterViewInit, OnDestroy {\n    public searchTerm = \"\";\n    public page = 1;\n    public sorter = {\n        columns: [\"color\", \"red\", \"green\", \"blue\"],\n        sortedColumn: \"color\",\n        direction: \"asc\",\n    };\n\n    public state: INovaFilteringOutputs = {\n        repeat: {\n            itemsSource: [],\n        },\n        paginator: {\n            // @ts-ignore: used for demo purposes\n            total: undefined,\n        },\n    };\n\n    public filters: any[];\n    public selectedFilters: any[];\n\n    private delayActionSubject: Subject<any> = new Subject();\n    private outputsSubscription: Subscription;\n\n    @ViewChild(\"filteringPaginator\") filteringPaginator: PaginatorComponent;\n    @ViewChild(\"filteringSearch\") filteringSearch: SearchComponent;\n    @ViewChild(\"filteringSorter\") filteringSorter: SorterComponent;\n\n    constructor(public dataSourceService: ClientSideDataSource<any>,\n                public changeDetection: ChangeDetectorRef) {\n        dataSourceService.setData(INITIAL_ARRAY);\n\n        this.filters = [\"regular\", \"dark\", \"light\"];\n        this.selectedFilters = [];\n    }\n\n    async ngAfterViewInit() {\n        this.dataSourceService.componentTree = {\n            search: {\n                componentInstance: this.filteringSearch,\n            },\n            paginator: {\n                componentInstance: this.filteringPaginator,\n            },\n        };\n        this.outputsSubscription = this.dataSourceService.outputsSubject.subscribe((data: INovaFilteringOutputs) => {\n            this.state = data;\n            this.changeDetection.detectChanges();\n        });\n        this.delayActionSubject.pipe(debounceTime(500)).subscribe(() => {\n            this.dataSourceService.applyFilters();\n        });\n\n        await this.dataSourceService.applyFilters();\n    }\n\n    ngOnDestroy() {\n        this.outputsSubscription.unsubscribe();\n    }\n\n    public onSearch() {\n        this.delayActionSubject.next();\n    }\n\n    public async changePagination() {\n        await this.dataSourceService.applyFilters();\n    }\n\n}\n";