export default "import { AfterViewInit, ChangeDetectorRef, Component, Injectable, OnDestroy, ViewChild } from \"@angular/core\";\nimport {\n    ClientSideDataSource,\n    INovaFilteringOutputs,\n    PaginatorComponent,\n    SearchComponent,\n    SearchService,\n} from \"@nova-ui/bits\";\nimport { Subscription } from \"rxjs\";\n\nconst RANDOM_ARRAY = [\n    {color: \"regular-blue\"},\n    {color: \"regular-green\"},\n    {color: \"regular-yellow\"},\n    {color: \"regular-cyan \"},\n    {color: \"regular-magenta\"},\n    {color: \"regular-black\"},\n    {color: \"dark-blue\"},\n    {color: \"dark-green\"},\n    {color: \"dark-yellow\"},\n    {color: \"dark-cyan \"},\n    {color: \"dark-magenta\"},\n    {color: \"dark-black\"},\n    {color: \"light-blue\"},\n    {color: \"light-green\"},\n    {color: \"light-yellow\"},\n    {color: \"light-cyan \"},\n    {color: \"light-magenta\"},\n    {color: \"light-black\"},\n];\n\ninterface ExampleItem {\n    color: string;\n}\n\n@Injectable()\nexport class ClientSideCustomSearchService extends SearchService {\n    protected filterPredicate(item: any, searchValue: any): boolean {\n        return item.toString().indexOf(searchValue.toString()) !== -1;\n    }\n}\n\n@Component({\n    selector: \"nui-client-side-custom-search-example\",\n    templateUrl: \"./client-side-custom-search.example.component.html\",\n    providers: [ClientSideDataSource, {provide: SearchService, useClass: ClientSideCustomSearchService}],\n})\nexport class DataSourceClientSideCustomSearchExampleComponent implements AfterViewInit, OnDestroy {\n    public searchTerm = \"\";\n    public page = 1;\n\n    public state: INovaFilteringOutputs = {\n        repeat: {\n            itemsSource: [],\n        },\n        paginator: {\n            // @ts-ignore: used for demo purposes\n            total: undefined,\n        },\n    };\n\n    public filters: any[];\n    public selectedFilters: any[];\n\n    @ViewChild(\"filteringPaginator\") filteringPaginator: PaginatorComponent;\n    @ViewChild(\"filteringSearch\") filteringSearch: SearchComponent;\n\n    private outputsSubscription: Subscription;\n\n    constructor(public dataSourceService: ClientSideDataSource<ExampleItem>,\n                public changeDetection: ChangeDetectorRef) {\n        dataSourceService.setData(RANDOM_ARRAY);\n\n        this.filters = [\"regular\", \"dark\", \"light\"];\n        this.selectedFilters = [];\n    }\n\n    async ngAfterViewInit() {\n        this.dataSourceService.componentTree = {\n            search: {\n                componentInstance: this.filteringSearch,\n            },\n            paginator: {\n                componentInstance: this.filteringPaginator,\n            },\n        };\n        this.outputsSubscription = this.dataSourceService.outputsSubject.subscribe((data: INovaFilteringOutputs) => {\n            this.state = data;\n            this.changeDetection.detectChanges();\n        });\n        await this.dataSourceService.applyFilters();\n    }\n\n    ngOnDestroy() {\n        this.outputsSubscription.unsubscribe();\n    }\n\n    public async onSearch(value: string) {\n        await this.dataSourceService.applyFilters();\n    }\n\n    public async changePagination() {\n        await this.dataSourceService.applyFilters();\n    }\n\n    public async applyFilters() {\n        await this.dataSourceService.applyFilters();\n    }\n}\n";