export default "import {Injectable} from \"@angular/core\";\nimport {Observable, of} from \"rxjs\";\nimport {delay} from \"rxjs/operators\";\n\nexport interface FoodNode {\n    name: string;\n    page?: number;\n    children?: FoodNode[];\n    isLoading?: boolean;\n    hasPagination?: boolean;\n}\n\nexport interface IApiResponse {\n    items: FoodNode[];\n    total: number;\n}\n\n@Injectable()\nexport class HttpMock {\n    private fruitsList = [\n        $localize `apple`,\n        $localize `orange`,\n        $localize `banana`,\n        $localize `watermelon`,\n        $localize `peach`,\n        $localize `pineapple`,\n        $localize `lemon`,\n        $localize `mango`,\n    ];\n    private vegetablesList = [\n        $localize `tomato`,\n        $localize `cucumber`,\n        $localize `cabbage`,\n        $localize `pepper`,\n        $localize `carrot`,\n        $localize `onion`,\n        $localize `broccoli`,\n        $localize `corn`,\n    ];\n\n    private totalItems = 1337;\n\n    getNodeItems(nodeId: string, page: number, pageSize: number): Observable<IApiResponse> {\n        // nodeId can be handled on API depending on app needs\n        const itemList = nodeId === \"Vegetables\"\n            ? this.vegetablesList\n            : this.fruitsList;\n\n        const totalItemList = this.getTotalItemList(itemList, this.totalItems/itemList.length);\n\n        const items: FoodNode[] = Array.from({ length: pageSize }).map((i, ind) => ({\n            name: totalItemList[ind],\n            page,\n        }));\n\n        const response = {\n            items,\n            total: this.totalItems,\n        };\n        return of(response).pipe(delay(1000));\n    }\n\n    private getTotalItemList(list: any[], times: number) {\n        const totalArray = [];\n        for (let i = 0; i < times; i++) {\n            totalArray.push(...list)\n        }\n        return totalArray;\n    }\n}\n\n\nexport const TREE_DATA_PAGINATOR: FoodNode[] = [\n    {\n        name: \"Food\",\n        children: [\n            {\n                name: \"Vegetables\",\n                children: [],\n                hasPagination: true,\n            },\n            {\n                name: \"Fruits\",\n                children: [],\n                hasPagination: true,\n            },\n        ],\n    },\n\n];\n\nexport const TREE_DATA: FoodNode[] = [\n    {\n        name: \"Fruit\",\n        children: [\n            { name: \"Apple\" },\n            { name: \"Banana\" },\n            { name: \"Fruit loops\" },\n        ],\n    },\n    {\n        name: \"Vegetables\",\n        children: [\n            {\n                name: \"Green\",\n                children: [{ name: \"Broccoli\" }, { name: \"Brussels sprouts\" }],\n            },\n            {\n                name: \"Orange\",\n                children: [{ name: \"Pumpkins\" }, { name: \"Carrots\" }],\n            },\n            {\n                name: \"Red\",\n            },\n        ],\n    },\n    {\n        name: \"Meat\",\n    },\n];\n\nexport const TREE_DATA_CHECKBOX: FoodNode[] = [\n    {\n        name: \"Vegetables\",\n        children: [\n            {\n                name: \"Green\",\n                children: [{ name: \"Broccoli\" }, { name: \"Brussels sprouts\" }],\n            },\n            {\n                name: \"Orange\",\n                children: [{ name: \"Pumpkins\" }, { name: \"Carrots\" }],\n            },\n            {\n                name: \"Red\",\n            },\n        ],\n    },\n    {\n        name: \"Fruit\",\n        children: [\n            { name: \"Apple\" },\n            { name: \"Banana\" },\n            { name: \"Fruit loops\" },\n        ],\n    },\n    {\n        name: \"Meat\",\n    },\n];\n";