export default "import { Injectable } from \"@angular/core\";\nimport { Observable, of } from \"rxjs\";\nimport { delay } from \"rxjs/operators\";\n\ninterface FoodNode {\n    name: string;\n    page?: number;\n    children?: FoodNode[];\n    isLoading?: boolean;\n    hasPagination?: boolean;\n}\n\ninterface IApiResponse {\n    items: FoodNode[];\n    total: number;\n}\n\n@Injectable()\nexport class HttpMockService {\n    private fruitsList = [\n        $localize `apple`,\n        $localize `orange`,\n        $localize `banana`,\n        $localize `watermelon`,\n        $localize `peach`,\n        $localize `pineapple`,\n        $localize `lemon`,\n        $localize `mango`,\n    ];\n    private vegetablesList = [\n        $localize `tomato`,\n        $localize `cucumber`,\n        $localize `cabbage`,\n        $localize `pepper`,\n        $localize `carrot`,\n        $localize `onion`,\n        $localize `broccoli`,\n        $localize `corn`,\n    ];\n\n    private totalItems = 1337;\n\n    getNodeItems(nodeId: string, page: number, pageSize: number): Observable<IApiResponse> {\n        // nodeId can be handled on API depending on app needs\n        const itemList = nodeId === \"Vegetables\"\n            ? this.vegetablesList\n            : this.fruitsList;\n\n        const items: FoodNode[] = Array.from({ length: pageSize }).map(() => ({\n            name: this.getRandomFrom(itemList),\n            page,\n        }));\n\n        const response = {\n            items,\n            total: this.totalItems,\n        };\n        return of(response).pipe(delay(1000));\n    }\n\n    private getRandomFrom(list: any[]) {\n        return list[Math.floor(Math.random() * list.length)];\n    }\n}\n";